@import '../common/styles/sass/variables';

.nav {
  width: 600px;
  display: flex;
  justify-content: space-between;
  margin-right: 20px;

  @media screen and (max-width: 760px) {
    margin: 0 auto;
    padding: 0 10px;
  }

  a {
    position: relative;
    text-decoration: none;
    color: #fff;

    &:hover {
      &:after {
        content: '';
        width: 100%;
        height: 2px;
        background: #fff;
        position: absolute;
        bottom: 0;
        left: 0;
        transition: all .5s;
      }
    }
  }
}

.scroll a {
  color: $primaryColor;

  &:hover {
    &:after {
      background: $primaryColor;
    }
  }
}