@import '../common/styles/sass/variables';

.header {
    height: 80px;

    display: flex;
    justify-content: flex-end;
    align-items: center;

    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 10;

    @media screen and (max-width: 600px) {
        justify-content: center;
    }
}