@import '../../../common/styles/sass/variables';
@import '../../../common/styles/sass/mixins';

.infoItem {
  width: 200px;
  padding: 0 20px 50px;
  margin-bottom: 30px;

  @include flex(column);

  @media screen and (max-width: 760px) {
    width: 150px;
  }

  @media screen and (max-width: 620px) {
    width: 100px;
    padding: 5px
  }

  @media screen and (max-width: 380px) {
    width: 100%;
    flex-direction: row;
  }

  &:hover .icon {
    color: $textSecondColor;
    background: $primaryColor;
  }

  .icon {
    width: 70px;
    height: 70px;
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 70px;
    background: $bgColor;
    box-shadow: 0 10px 30px -8px rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    transition: all .5s;

    @include flex();

    @media screen and (max-width: 620px) {
      width: 60px;
      height: 60px;
    }

    @media screen and (max-width: 380px) {
      margin-bottom: 0;
    }
  }

  .titleAndText {
    @include flex(column);

    @media screen and (max-width: 380px) {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      margin-left: 20px;
    }

    h6 {
      margin-bottom: 15px;
      font-weight: 800;
      font-size: 18px;

      @media screen and (max-width: 620px) {
        font-size: 16px;
      }

      @media screen and (max-width: 380px) {
        margin-bottom: 5px;
        font-size: 18px;
      }
    }

    p {
      margin: 0;
      word-spacing: 2px;
      font-size: 14px;
      line-height: 1.6;
      color: $textColor;
      text-align: center;

      @media screen and (max-width: 620px) {
        font-size: 10px;
      }

      @media screen and (max-width: 380px) {
        font-size: 14px;
      }
    }
  }
}
